import Skeleton from 'react-loading-skeleton';
import 'react-loading-skeleton/dist/skeleton.css';
const CardDailyUi = () => {
  return (
    <div className="dailyui__item">
      <figure className="dailyui__image"><Skeleton height={180} /></figure>
      <h2 className="dailyui__title"><Skeleton /></h2>
      <p className="dailyui__description"><Skeleton /></p>
    </div>
  )
}

export default CardDailyUi